$invalid-state-color: #fbfbfb;
$valid-state-color: #00a4a5;
$hover-state-color: #007b7b;

.interactive-map {
  // scss-lint:enable all
  width: 100%;
  height: 50%;
  max-width: 500px;
  margin: auto;
  display: block;

  .state {
    fill: $valid-state-color;
    cursor: pointer;

    &:hover,
    &.active {
      fill: $hover-state-color;
    }
  }
}


